import { HttpClient } from '@angular/common/http';
import { ModuleWithProviders, NgModule, Provider } from '@angular/core';
import { TranslateCompiler, TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { TranslateMessageFormatCompiler } from 'ngx-translate-messageformat-compiler';

// Factory function for HTTP Loader
export function httpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

// Factory function for Message Format Compiler
export function translateCompilerFactory() {
  return new TranslateMessageFormatCompiler();
}

// Providers
const translateLoader: Provider = {
  provide: TranslateLoader,
  useFactory: httpLoaderFactory,
  deps: [HttpClient]
};

const translateCompiler: Provider = {
  provide: TranslateCompiler,
  useFactory: translateCompilerFactory
};

@NgModule({
  imports: [TranslateModule]
})
export class AppTranslateModule {
  static forRoot(): ModuleWithProviders<AppTranslateModule> {
    return {
      ngModule: AppTranslateModule,  // Use AppTranslateModule instead of TranslateModule
      providers: [translateLoader, translateCompiler]
    };
  }

  static forChild(): ModuleWithProviders<AppTranslateModule> {
    return {
      ngModule: AppTranslateModule,  // Use AppTranslateModule instead of TranslateModule
      providers: [translateLoader, translateCompiler],
    };
  }
}