module.exports = "\r\n.title{\r\n  cursor: pointer;\r\n}\r\n.title span:nth-child(1) {\r\n    color: #7ef0f3;\r\n    font-weight: bold;\r\n  }\r\n.title span:nth-child(2) {\r\n    color: #0357A8;\r\n    font-weight: bold;\r\n  }\r\n.search-container {\r\n    position: relative;\r\n    width: 100%;\r\n    max-width: 300px;\r\n  }\r\n.search-container input {\r\n    width: 100%;\r\n    padding: 10px 40px 10px 15px;\r\n    border: none;\r\n    border-radius: 20px;\r\n    outline: none;\r\n    background-color: #7eeff329;\r\n    font-size: 16px;\r\n  }\r\n.search-container i {\r\n    position: absolute;\r\n    right: 15px;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n    color: #7eeff3f9;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n  }\r\n.start-div{\r\n    width:35%;\r\n  }\r\n/* Centering the div */\r\n.centered-div {\r\n  width: 40%;\r\n  text-align: center;\r\n}\r\n/* Styling the list items */\r\n.centered-div ul li a {\r\n  text-decoration: none;\r\n  color: #000;\r\n  font-weight: normal;\r\n  position: relative;\r\n  transition: all 0.3s ease-in-out;\r\n}\r\n/* Hover effect */\r\n.centered-div ul li a:hover,\r\n.centered-div ul li a.active-link {\r\n  color: #0357A8;\r\n  font-weight: bold;\r\n}\r\n/* Underline effect on hover and active */\r\n.centered-div ul li a::after {\r\n  content: '';\r\n  position: absolute;\r\n  left: 0;\r\n  bottom: -2px;\r\n  width: 0;\r\n  height: 2px;\r\n  background-color: #0357A8;\r\n  transition: width 0.3s ease-in-out;\r\n}\r\n.centered-div ul li a:hover::after,\r\n.centered-div ul li a.active-link::after {\r\n  width: 100%;\r\n}\r\n/* Breadcrumb */\r\n.breadcrumb {\r\n    background-color: #0357A8;\r\n    padding: 2px 10px;\r\n    border-radius: 5px;\r\n  }\r\n.breadcrumb-item a {\r\n    text-decoration: none;\r\n    color: white;\r\n  }\r\n.breadcrumb-item.active {\r\n    font-weight: bold;\r\n    color: white;\r\n  }\r\n/* Dropdown Container */\r\n.custom-dropdown {\r\n  margin: 0 10px;\r\n  position: relative;\r\n  border: none;\r\n  display: inline-block;\r\n}\r\n/* Dropdown Button */\r\n.dropdown-btn {\r\n  background: transparent;\r\n  border: none;\r\n  border-radius: 5px;\r\n  padding: 10px 15px;\r\n  cursor: pointer;\r\n  transition: all 0.3s ease-in-out;\r\n  font-size: 16px;\r\n}\r\n.dropdown-btn:hover {\r\n  background-color: #f0f0f0;\r\n}\r\n/* Dropdown Menu */\r\n.dropdown-menu {\r\n  display: none;\r\n  position: absolute;\r\n  top: 100%;\r\n  left: 0;\r\n  background-color: #fff;\r\n  border-radius: 5px;\r\n  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);\r\n  z-index: 1000;\r\n  list-style: none;\r\n  padding: 0;\r\n  margin: 5px 0 0;\r\n  min-width: 120px;\r\n}\r\n/* Show Dropdown */\r\n.custom-dropdown .dropdown-menu.show {\r\n  display: block;\r\n}\r\n/* Dropdown Item */\r\n.dropdown-item {\r\n  padding: 10px 15px;\r\n  text-decoration: none;\r\n  color: #333;\r\n  display: block;\r\n  transition: background-color 0.3s ease-in-out;\r\n}\r\n.dropdown-item:hover {\r\n  background-color: #f0f0f0;\r\n  color: #000;\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"