import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Product } from 'src/app/shared/interfaces/product';
import { Type } from 'src/app/shared/interfaces/type';
import { ProductService } from 'src/app/shared/services/product.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

  constructor(private _productService:ProductService) { }
products:Product[] =[];
productsArray:any[] =[];
types:Type[] =[];
filterForm!:FormGroup;
filteredProducts:Product[] =[];
allTypes: any = {};  

werehouses: any = [
  { text: 'Warehouse 1', value: 'wh1' },
  { text: 'Warehouse 2', value: 'wh2' }
];

ngOnInit() {
  // Initialize Form
  this.filterForm = new FormGroup({
    wereHouse: new FormControl(null, Validators.required),
    type: new FormControl(null, Validators.required),
    zeroBalance: new FormControl({ value: false, disabled: true }),
    classification: new FormControl({ value: 'all', disabled: true }),
    products:new FormControl([])
  });

  this.getAllProducts();
  this.getAllTypes();

  // Listen for changes to the warehouse selection
  this.filterForm.get('wereHouse').valueChanges.subscribe((selectedWarehouse) => {
    this.updateTypes(selectedWarehouse);
  });
  this.filterForm.valueChanges.subscribe(() => {
    setTimeout(() => {
      const wereHouseSelected = this.filterForm.get('wereHouse').valid;
      const typeSelected = this.filterForm.get('type').valid;
      const classification = this.filterForm.get('classification').value;

      if (wereHouseSelected && typeSelected) {
        this.filterForm.get('zeroBalance').enable();
        this.filterForm.get('classification').enable();
      } else {
        this.filterForm.get('zeroBalance').disable();
        this.filterForm.get('classification').disable();
      }
      if (classification === 'specific') { 
        this.filterForm.get('products').setValidators(Validators.required);
      } else {
        this.filterForm.get('products').clearValidators();
      }
      this.filterForm.get('products').updateValueAndValidity();
    }, 0);
  });
}

// Get all products
getAllProducts() {
  this._productService.getProducts().subscribe((res: Product[]) => {
    this.products = res;
    this.productsArray = res.map(product => product.name);
    this.filteredProducts = this.products;
  });
}

// Get all types and group them by warehouse
getAllTypes() {
  this._productService.getTypes().subscribe((res: any[]) => {
    this.types =res;
    this.allTypes = res.reduce((acc, type) => {
      (acc[type.warehouse] = acc[type.warehouse] || []).push(type);
      return acc;
    }, {});

    console.log('Grouped types:', this.allTypes);
  });
}

updateTypes(selectedWarehouse: string) {
  if (!selectedWarehouse) {
    this.types = [];
    this.productsArray = []; 
    return;
  }

  this.types = this.allTypes[selectedWarehouse] || [];
    this.productsArray = this.products
    .filter(product => product.warehouse === selectedWarehouse)
    .map(product => product.name);

  this.filterForm.get('type').setValue(null);
  this.filterForm.get('products').setValue(null);
}
onSubmit() {
  const { wereHouse, type, zeroBalance, classification, products } = this.filterForm.value;
  this.filteredProducts = this.products.filter(p => {
    let isMatch = true;

    // Filter by Type
    if (type.name && p.type !== type.name) {
      isMatch = false;
    }
    
    // Filter by Warehouse
    if (wereHouse && p.warehouse !== wereHouse) {
      isMatch = false;
    }
    
    // Filter by Zero Balance
    if (zeroBalance && p.quantity !== 0) {
      isMatch = false;
    }
    
    // Filter by Product Classification
    if (classification === 'specific' && products && products.length > 0) {
      if (!products.includes(p.name)) {
        isMatch = false;
      }
    }

    return isMatch;
  });
}
}
